<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="input-field col s12">
                    <input type="text" v-model="employee_id" required
                    disabled> 
                   
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="name" required> 
                     
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="dept" required> 
                     
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="excellence" required> 
                      
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="nickname" required> 
                      
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-lin to="/" class="btn grey">Cancel</router-lin>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'edit-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            excellence:null,
            nickname:null,
        }
    },
    created(){
        this.fetchData()
    },
//    beforeRouteEnter(to,from,next){
//         db.collection('emplyees').where('employee_id','==',to.params.employee_id).get()
//         .then(querySnapshot=>{
//             querySnapshot.forEach(doc => {
//                 next(vm=>{
//                     vm.employee_id=doc.data().employee_id
//                     vm.name=doc.data().name
//                     vm.dept=doc.data().dept
//                     vm.excellence.data().excellence
//                     vm.nickname=doc.data().nickname
                    
//                 })
//             })
            
//         })
        
//     },
    // watch:{
    //     '$route': 'fetchData'
    // },

    methods:{
        fetchData(){
           console.log('ok')
           db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get()
            .then (querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    this.employee_id=doc.data().employee_id
                    this.name=doc.data().name
                    this.dept=doc.data().dept
                    this.excellence=doc.data().excellence
                    this.nickname=doc.data().nickname
                })
            })
        },
        updateEmployee(){
            db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get()
            .then (querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    doc.ref.update({
                        name:this.name,
                        dept:this.dept,
                        excellence:this.excellence,
                        nickname:this.nickname
                    })
                    .then(()=>{
                        this.$router.push({name:'view-employee',params:{employee_id:this.employee_id}})
                    })
                })
            })
        }
}
}
</script>


