<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="input-field col s12">
                    <input type="text" v-model="employee_id" required> 
                    <label >Employee ID#</label>  
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="name" required> 
                    <label >Name</label>  
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="dept" required> 
                    <label >Department</label>  
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="excellence" required> 
                    <label >Excellence</label>  
                </div>
                <div class="input-field col s12">
                    <input type="text" v-model="nickname" required> 
                    <label >Nickname</label>  
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-lin to="/" class="btn grey">Cancel</router-lin>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'new-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            excellence:null,
            nickname:null,
        }
    },
    methods:{
        saveEmployee(){
            db.collection('employees').add({
                employee_id:this.employee_id,
                name:this.name,
                dept:this.dept,
                excellence:this.excellence,
                nickname:this.nickname
            })
            .then(docRef=>{
                this.$router.push('/')
            })
            .catch(err=>{
                throw err
            })
        }
    }
}
</script>


