<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{nickname}}</h4> </li>
            <li class="collection-item"><strong>EMPLOYEE_ID</strong> # {{employee_id}}</li>
            <li class="collection-item"><strong>NAME: </strong>{{name}}</li>
            <li class="collection-item"><strong>DEPARTMENT: </strong>{{dept}}</li>
            <li class="collection-item"><strong>EXCELLENCE: </strong>{{excellence}}</li>
            
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteEmployee()" class="btn red">Delete</button>
        <div class="fixed-action-btn">
        <router-link :to="{name:'edit-employee',params:{employee_id:this.employee_id}}" class="btn-floating btn-large orange">
        <i class="fa fa-pencil"></i>
        
        </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name:'view-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            dept:null,
            excellence:null,
            nickname:null
        }
    },
    created(){
        this.fetchData()
    },
//    beforeRouteEnter(to,from,next){
//         db.collection('emplyees').where('employee_id','==',to.params.employee_id).get()
//         .then(querySnapshot=>{
//             querySnapshot.forEach(doc => {
//                 next(vm=>{
//                     vm.employee_id=doc.data().employee_id
//                     vm.name=doc.data().name
//                     vm.dept=doc.data().dept
//                     vm.excellence.data().excellence
//                     vm.nickname=doc.data().nickname
                    
//                 })
//             })
            
//         })
        
//     },
    // watch:{
    //     '$route': 'fetchData'
    // },

    methods:{
        fetchData(){
           console.log('ok')
           db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get()
            .then (querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    this.employee_id=doc.data().employee_id
                    this.name=doc.data().name
                    this.dept=doc.data().dept
                    this.excellence=doc.data().excellence
                    this.nickname=doc.data().nickname
                })
            })
        },
        deleteEmployee(){
            if (confirm('Are you sure to delete?'))
            {
            db.collection('employees').where('employee_id','==',this.employee_id).get()
            .then(querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            
        }
    }
    } 
}
</script>


